___CSS_LOADER_EXPORT___.push([module.id, `.home-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: calc(100vh - 80px);
  padding: 40px 24px 32px;
  text-align: center;
  transition: background-color 0.3s ease;
}

.home-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  max-width: 360px;
}

.home-title {
  font-size: 28px;
  font-weight: 600;
  color: #1d2939;
}

.home-goal-button {
  background: #1a73e8;
  color: #ffffff;
  border: none;
  border-radius: 18px;
  padding: 14px 24px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  max-width: 280px;
  transition: background 0.2s ease-in-out, transform 0.2s ease-in-out;
}

.home-goal-button:active {
  transform: scale(0.98);
}

.home-goal-button:hover {
  background: #1866d2;
}

.home-goal-status {
  font-size: 18px;
  font-weight: 500;
  color: #1d2939;
  padding: 12px 18px;
  border-radius: 14px;
  background: rgba(26, 115, 232, 0.08);
  width: 100%;
  max-width: 320px;
}

.home-goal-status--success {
  background: rgba(16, 185, 129, 0.12);
  color: #047857;
}

.home-goal-progress {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
  color: #475467;
}

.home-illustration {
  margin-top: auto;
  display: flex;
  justify-content: center;
  width: 100%;
}

.home-illustration-avatar {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: min(220px, 100%);
}

.home-illustration-image {
  max-width: 220px;
  width: 100%;
  height: auto;
}

.home-illustration-hat {
  position: absolute;
  width: 70%;
  max-width: 150px;
  left: 50%;
  bottom: 60%;
  transform: translateX(-50%);
  pointer-events: none;
}

.home-actions {
  display: flex;
  justify-content: center;
  margin-top: 28px;
}

.home-customize-button {
  border: none;
  border-radius: 18px;
  padding: 12px 20px;
  background: rgba(59, 130, 246, 0.14);
  color: #1d4ed8;
  font-weight: 600;
  font-size: 15px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  transition: background 0.2s ease, transform 0.2s ease;
}

.home-customize-button:hover {
  background: rgba(59, 130, 246, 0.22);
  transform: translateY(-1px);
}

.home-customize-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #ef4444;
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
}

@media (max-width: 380px) {
  .home-page {
    padding: 32px 20px 24px;
  }

  .home-title {
    font-size: 24px;
  }

  .home-goal-status {
    font-size: 16px;
  }

  .home-customize-button {
    width: 100%;
    justify-content: center;
  }
}





